import React from "react";
import styled from "styled-components";
import {
  GmarketBold,
  GmarketMedium,
  NoteSansBold,
  NoteSansLight,
} from "../../common/Text/Text";
import { PRIMARY_COLOR } from "../../common/color/color";

const HistorySection = styled.section`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10rem;
  background-color: #f6f6f9;
`;

const TitleContainer = styled.div`
  width: 27vw;
  height: 5vw;
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 7.5vw;
`;

const HistoryContainer = styled.div`
  width: 50vw;
  height: 45vw;
  display: flex;
  flex-direction: row;
`;

const YearColumn = styled.div`
  flex: 2.7;
  display: flex;
  flex-direction: column;
`;

const ScaleColumn = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
`;

const DescriptionColumn = styled.div`
  flex: 25;
  display: flex;
  flex-direction: column;
  margin-left: 0.5vw;
`;

const History2021 = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
`;

const History2022 = styled.div`
  flex: 2;
  display: flex;
  flex-direction: column;
`;

const ScaleUnit = styled.div`
  flex: 1;
  border-bottom: #0d00aa 2px solid;
  border-left: #0d00aa 3px solid;
  margin-right: 1vw;
  position: relative;
`;

const BulletinButton = styled.div`
  position: absolute;
  width: 0px;
  left: 0;
  top: 0;
  border: 0.4vw solid #0d00aa;
  border-radius: 0.4vw;
  transform: translateY(-50%) translateX(-65%);
`;

const ArrowHead = styled.div`
  position: absolute;
  width: 2vw;
  left: 0;
  bottom: 0;
  transform-origin: bottom left;
  transform: rotate(-45deg);
  border-bottom: 0.2vw solid #0d00aa;
`;

const LeftBottomAlignCell = ({
  children,
  flexRatio = 1,
  fontSize,
  fontType,
  fontColor = "#737373",
}) => {
  return (
    <div style={{ position: "relative", flex: flexRatio }}>
      <div
        style={{
          position: "absolute",
          left: 0,
          bottom: 0,
          transform: `translateY(50%)`,
        }}
      >
        {fontType === "NoteSansLight" && (
          <NoteSansLight fontSize={fontSize}>{children}</NoteSansLight>
        )}
        {fontType === "GmarketBold" && (
          <GmarketBold fontSize={fontSize} fontColor={fontColor}>
            {children}
          </GmarketBold>
        )}
        {fontType === "GmarketMedium" && (
          <GmarketMedium fontSize={fontSize}>{children}</GmarketMedium>
        )}
      </div>
    </div>
  );
};

const history2021Dates = ["11", "12", ""];
const history2022Dates = ["2", "3", "4", "6", "8", "8","9","10","11",""];

const description2021List = [
  "()  ",
  "         ",
  "2022",
];
const description2022List = [
  "      ",
  `"   "  "   )`,
  "    ",
  `  ( ) `,
  "   ",
  "  11 ",
  `"      "  `,
  "      ",
  "    ",
  "",
];

export const History = () => {
  return (
    <HistorySection>
      <TitleContainer>
        <GmarketBold
          fontColor={PRIMARY_COLOR}
          fontSize={"3vw"}
          fontWeight={"bold"}
        >
           
        </GmarketBold>
      </TitleContainer>
      <HistoryContainer>
        <YearColumn>
          <History2021>
            {history2021Dates.map((val, idx) => {
              if (idx === 0) {
                return (
                  <LeftBottomAlignCell
                    children={val}
                    fontSize={"1.3vw"}
                    fontColor="#0D00AA"
                    fontType="GmarketBold"
                  />
                );
              }
              if (idx === 2) {
                // 12.9     
                return (
                  <LeftBottomAlignCell
                    children={val}
                    flexRatio={1.3}
                    fontSize={"1.5vw"}
                  />
                );
              }
              return (
                <LeftBottomAlignCell
                  children={val}
                  fontSize={"1.3vw"}
                  fontType={"GmarketMedium"}
                />
              );
            })}
          </History2021>
          <History2022>
            {history2022Dates.map((val, idx) => {
              return (
                <LeftBottomAlignCell
                  children={val}
                  fontSize={"1.3vw"}
                  fontType={"GmarketMedium"}
                />
              );
            })}
          </History2022>
        </YearColumn>
        <ScaleColumn>
          <History2021>
            <ScaleUnit>
              <BulletinButton />
            </ScaleUnit>
            <ScaleUnit />
            <ScaleUnit style={{ borderBottom: "none", flex: 1.3 }} />
          </History2021>
          <History2022>
            <ScaleUnit>
              <BulletinButton />
            </ScaleUnit>
            <ScaleUnit />
            <ScaleUnit />
            <ScaleUnit />
            <ScaleUnit />
            <ScaleUnit style={{ borderBottom: "none" }}>
              <ArrowHead />
            </ScaleUnit>
          </History2022>
        </ScaleColumn>
        <DescriptionColumn>
          <History2021>
            {description2021List.map((val, idx) => {
              if (idx === 0) {
                //2021  
                return (
                  <div style={{ position: "relative", flex: 1 }}>
                    <div
                      style={{
                        position: "absolute",
                        left: 0,
                        top: 0,
                        transform: `translateY(-50%)`,
                      }}
                    >
                      <GmarketBold fontSize={"2vw"} fontColor="#0D00AA">
                        2021
                      </GmarketBold>
                    </div>
                    <div
                      style={{
                        position: "absolute",
                        left: 0,
                        bottom: 0,
                        transform: `translateY(50%)`,
                      }}
                    >
                      <NoteSansBold fontSize={"1.3vw"} fontColor="#0D00AA">
                        {val}
                      </NoteSansBold>
                    </div>
                  </div>
                );
              }
              if (idx === 2)
                // 2022  
                return (
                  <LeftBottomAlignCell
                    children={val}
                    flexRatio={1.3}
                    fontSize={"2vw"}
                    fontType={"GmarketBold"}
                    fontColor="#0D00AA"
                  />
                );
              return (
                <LeftBottomAlignCell
                  children={val}
                  fontSize={"1.3vw"}
                  fontType={"NoteSansLight"}
                />
              );
            })}
          </History2021>
          <History2022>
            {description2022List.map((val, idx) => {
              return (
                <LeftBottomAlignCell
                  children={val}
                  fontSize={"1.3vw"}
                  fontType={"NoteSansLight"}
                />
              );
            })}
          </History2022>
        </DescriptionColumn>
      </HistoryContainer>
    </HistorySection>
  );
};

export default History;
